"use client";

import { useState, useRef, useEffect } from "react";
import {
  MapPin,
  Users,
  Search,
  ChevronDown,
  Plus,
  Minus,
} from "lucide-react";
import {
  Grid,
  TextField,
  InputAdornment,
  IconButton,
  Menu,
  MenuItem,
  Button,
  Box,
  Typography,
} from "@mui/material";
import { countriesAirports } from "../../data/countriesAirports";

const TourSearchForm = () => {
  // ------------------- HYDRATION FIX -------------------
  const [mounted, setMounted] = useState(false);
  useEffect(() => setMounted(true), []);

  // ------------------- CITY -------------------
  const [citySearch, setCitySearch] = useState("");
  const [selectedCity, setSelectedCity] = useState(null);
  const [cityAnchorEl, setCityAnchorEl] = useState(null);

  const allCities = countriesAirports.flatMap((country) =>
    country.cities.map((city) => ({
      city: city.city,
      country: country.country,
      code: city.code,
      airport: city.airport,
    }))
  );

  const filteredCities = allCities.filter(
    (item) =>
      item.city.toLowerCase().includes(citySearch.toLowerCase()) ||
      item.country.toLowerCase().includes(citySearch.toLowerCase())
  );

  const handleCityClick = (event) => {
    setCityAnchorEl(event.currentTarget);
  };

  const handleCityClose = () => {
    setCityAnchorEl(null);
  };

  const handleCitySelect = (city) => {
    setSelectedCity(city);
    setCitySearch(`${city.city}, ${city.country}`);
    handleCityClose();
  };

  // ------------------- DATES -------------------
  const [checkInDate, setCheckInDate] = useState("");
  const [checkOutDate, setCheckOutDate] = useState("");

  useEffect(() => {
    const today = new Date();
    const next = new Date(today);
    next.setDate(today.getDate() + 7);

    setCheckInDate(today.toISOString().split("T")[0]);
    setCheckOutDate(next.toISOString().split("T")[0]);
  }, []);

  // ------------------- TRAVELLERS -------------------
  const [travellers, setTravellers] = useState({
    adults: 1,
    children: 0,
    infants: 0,
  });

  const [travellerAnchorEl, setTravellerAnchorEl] = useState(null);

  const handleTravellerClick = (event) => {
    setTravellerAnchorEl(event.currentTarget);
  };

  const handleTravellerClose = () => {
    setTravellerAnchorEl(null);
  };

  const updateTraveller = (type, value) => {
    setTravellers((prev) => ({
      ...prev,
      [type]: Math.max(0, prev[type] + value),
    }));
  };

  const travellersLabel = `${travellers.adults} Adults, ${travellers.children} Children`;

  if (!mounted) return null;

  return (
    <Box sx={{ width: "100%" }}>
      <Grid container spacing={2} alignItems="center" margin={2} paddingTop={2} paddingBottom={2}>
        {/* CITY */}
        <Grid item xs={12} sm={4}>
          <TextField
            fullWidth
            size="small"
            placeholder="Search by City"
            value={selectedCity ? `${selectedCity.city}, ${selectedCity.country}` : citySearch}
            onChange={(e) => {
              setCitySearch(e.target.value);
              setCityAnchorEl(e.currentTarget);
            }}
            onClick={handleCityClick}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start" sx={{height:15, width:15}}>
                  <MapPin />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end" sx={{height:15, width:15}}>
                  <ChevronDown />
                </InputAdornment>
              ),
            }}
          />
          <Menu
            anchorEl={cityAnchorEl}
            open={Boolean(cityAnchorEl)}
            onClose={handleCityClose}
            PaperProps={{ style: { maxHeight: 250 } }}
          >
            {filteredCities.length > 0 ? (
              filteredCities.map((city, index) => (
                <MenuItem
                  key={`${city.city}-${city.country}-${index}`}
                  onClick={() => handleCitySelect(city)}
                >
                  {city.city}, {city.country}
                </MenuItem>
              ))
            ) : (
              <MenuItem disabled>No results found</MenuItem>
            )}
          </Menu>
        </Grid>

        {/* CHECK-IN */}
        <Grid item xs={12} sm={3} width={300}>
          <TextField
            fullWidth
            size="small"
            type="date"
            value={checkInDate}
            onChange={(e) => setCheckInDate(e.target.value)}
            InputProps={{ sx: { fontSize: 12, height: 40 } }}
          />
        </Grid>

        {/* TRAVELLERS */}
        <Grid item xs={12} sm={3}>
          <Button
            fullWidth
            variant="outlined"
            size="small"
            onClick={handleTravellerClick}
            sx={{ justifyContent: "space-between", fontSize: 12, textTransform: "none" }}
            endIcon={<Users sx={{height:"4", width:"4"}} />}
          >
            {travellersLabel}
          </Button>
          <Menu
            anchorEl={travellerAnchorEl}
            open={Boolean(travellerAnchorEl)}
            onClose={handleTravellerClose}
            PaperProps={{ style: { width: 250, padding: 8 } }}
          >
            {["adults", "children", "infants"].map((type) => (
              <Box key={type} sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", mb: 1 }}>
                <Typography sx={{ fontSize: 12 }}>{type.charAt(0).toUpperCase() + type.slice(1)}</Typography>
                <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
                  <IconButton size="small" onClick={() => updateTraveller(type, -1)}>
                    <Minus size={16} />
                  </IconButton>
                  <Typography sx={{ fontSize: 12 }}>{travellers[type]}</Typography>
                  <IconButton size="small" onClick={() => updateTraveller(type, 1)}>
                    <Plus size={16} />
                  </IconButton>
                </Box>
              </Box>
            ))}
          </Menu>
        </Grid>

       
          {/* Search Button */}
   <Grid item xs={12} sm={2}>
  <IconButton
    color="primary"
    size="small"
    sx={{
      backgroundColor: "#1976d2",
      "&:hover": { backgroundColor: "#115293" },
      width: "100%",
      height: "100%",
      borderRadius: 1, // removed border radius
    }}
  >
    <Search style={{ color: "white" }} />
  </IconButton>
</Grid>
      </Grid>
    </Box>
  );
};

export default TourSearchForm;
