"use client";

import {
  Box,
  Grid,
  Typography,
  Container,
  Card,
  CardMedia,
  Button,
} from "@mui/material";
import StarIcon from '@mui/icons-material/Star';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

/* ======================
   DATA WITH NEW IMAGES
====================== */
const hotels = [
  {
    image: "/styles/FeatureTour1.jpg",
    price: "USD 105.00",
    title: "Dubai",
    subtitle: "Stunning Dubai",
    rating: 5,
    width: "210px", // Smaller width for first 3 cards
  },
  {
    image: "/styles/FeatureTour2.jpg",
    price: "USD 320.00",
    title: "Petra",
    subtitle: "Day Visit of Petra From Oman",
    rating: 1,
    width: "210px", // Smaller width for first 3 cards
  },
  {
    image: "/styles/FeatureTour3.jpg",
    price: "USD 190.00",
    title: "Delhi",
    subtitle: "Old and New Delhi City Tour",
    rating: 2,
    width: "210px", // Smaller width for first 3 cards
  },
  // Card 4 - Simple image only (no hover effects, no content)
  {
    image: "/styles/bag.jpg",
    isSimpleImage: true, 
    width: "290px", 
  },
  {
    image: "styles/FeatureTour5.jpg",
    price: "USD 45.00",
    title: "Hong Kong",
    subtitle: "Hong Kong island tour",
    rating: 4,
    width: "430px", // Card 5 ki width 2 cards ke equal
  },
  {
    image: "styles/FeatureTour4.jpg",
    price: "USD 860.00",
    title: "Thailand", 
    subtitle: "6 Days around Thailand",
    rating: 4,
    width: "210px", 
  },
  {
    
    isSpecialCard: true,
    title: "Limited budget?",
    subtitle: "Find price drops and travel as far as you can with our exclusive deals.",
    buttonText: "View More",
    width: "240px", 
  },
];

/* ======================
   SIMPLE IMAGE CARD COMPONENT (Card 4)
====================== */
const SimpleImageCard = ({ image, width }) => {
  return (
    <Card
    >
      {/* Simple image container - no hover effects, no overlay */}
      <Box
        sx={{
          height: "200px",
          width: "100%",
          position: "relative",
          overflow: "hidden",
        }}
      >
        <CardMedia
          component="img"
          image={image}
          alt="Tour"
          sx={{
            height: "100%",
            width: "100%",
            objectFit: "cover",
            display: "block",
            backgroundColor: "#f5f5f5",
          }}
        />
      </Box>
    </Card>
  );
};

/* ======================
   SPECIAL CARD COMPONENT - WHITE BACKGROUND VERSION
====================== */
const SpecialCard = ({ title, subtitle, buttonText, width }) => {
  return (
    <Card
      sx={{
        width:  "255px",
        overflow: "hidden",
        boxShadow: "0px 6px 20px rgba(0,0,0,0.08)",
        transition: "all 0.3s ease",
        cursor: "pointer",
        position: "relative",
        height: "154px", 
        borderRadius: "0px",
        backgroundColor: "#eef4fb", 
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        p: 3,
      }}
    >
      {/* Text Content */}
      <Box
        sx={{
          textAlign: "start",
          mb: 3,
          width: "100%",
        }}
      >
        <Typography
          sx={{
            fontWeight: "700",
            color: "black",
            fontSize: "20px",
            mb: 1,
            lineHeight: 1.2,
            alignItems:"start"
          }}
        >
          {title}
        </Typography>
        
        <Typography
          sx={{
            fontWeight: "400",
            color: "text.secondary", //
            fontSize: "14px",
            lineHeight: 1.4,
          }}
        >
          {subtitle}
        </Typography>
      </Box>
      
      
      <Button
        variant="contained"
        endIcon={<ArrowForwardIcon />}
        sx={{
          backgroundColor: "#212529", 
          color: "white", 
        //  px:"45px",
        width:"100%",
          borderRadius: "8px",
          textTransform: "none",
          fontSize: "14px",
          "&:hover": {
            backgroundColor: "#145acf",
       
          },
     
          transition: "all 0.3s ease",
        }}
      >
        {buttonText}
      </Button>
    </Card>
  );
};

/* ======================
   REGULAR CARD COMPONENT - With Horizontal Line on Hover
====================== */
const HotelCard = ({ image, price, title, subtitle, rating, width }) => {
  return (
    <Card
      sx={{
        width: width || "225px", // Use width from data or default
        overflow: "hidden",
        boxShadow: "0px 6px 20px rgba(0,0,0,0.08)",
        transition: "all 0.3s ease",
        cursor: "pointer",
        borderRadius: "0px",
      }}
    >
      {/* Image Container with Hover Effects */}
      <Box
        sx={{
          height: "200px",
          width: "100%",
          position: "relative",
          overflow: "hidden",
          "&:hover .image-overlay": {
            backgroundColor: "rgba(0, 0, 0, 0.2)",
          },
          "&:hover .subtitle": {
            top: "50%",
            transform: "translateY(20%)",
          },
          "&:hover .price": {
            opacity: 1,
            visibility: "visible",
            bottom: "45px",
          },
          "&:hover .horizontal-line": {
            opacity: 1,
            visibility: "visible",
            bottom: "35px",
          },
          "&:hover .rating": {
            bottom: "10px",
          },
        }}
      >
        <CardMedia
          component="img"
          image={image}
          alt={title}
          sx={{
            height: "100%",
            width: "100%",
            objectFit: "cover",
            display: "block",
            backgroundColor: "#f5f5f5",
          }}
        />
        
        {/* Dark Overlay */}
        <Box
          className="image-overlay"
          sx={{
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            transition: "all 0.3s ease",
          }}
        />
        
        {/* Title - Top Left (Always visible) */}
        <Typography
          sx={{
            position: "absolute",
            top: "10px",
            left: "10px",
            color: "white",
            fontSize: "14px",
            fontWeight: "600",
            textShadow: "2px 2px 4px rgba(0, 0, 0, 0.5)",
            zIndex: 2,
          }}
        >
          {title}
        </Typography>
        
        {/* Subtitle - Moves to middle on hover */}
        <Typography
          className="subtitle"
          sx={{
            position: "absolute",
            bottom: "30px",
            left: "10px",
            color: "white",
            fontSize: "12px",
            fontWeight: "500",
            textShadow: "1px 1px 3px rgba(0, 0, 0, 0.7)",
            zIndex: 2,
            maxWidth: "90%",
            transition: "all 0.3s ease",
          }}
        >
          {subtitle}
        </Typography>
        
        {/* Price - Shows in middle between subtitle and rating on hover */}
        <Typography
          className="price"
          sx={{
            position: "absolute",
            left: "10px",
            bottom: "20px",
            color: "white",
            fontSize: "13px",
            fontWeight: "700",
            textShadow: "1px 1px 3px rgba(0, 0, 0, 0.7)",
            zIndex: 2,
            opacity: 0,
            visibility: "hidden",
            transition: "all 0.3s ease",
          }}
        >
          {price}
        </Typography>
        
        {/* Horizontal Line - Below Price on Hover */}
        <Box
          className="horizontal-line"
          sx={{
            position: "absolute",
            left: "10%",
            right: "10%",
            bottom: "25px",
            height: "1px",
            backgroundColor: "white",
            opacity: 0.7,
            opacity: 0,
            visibility: "hidden",
            transition: "all 0.3s ease",
            zIndex: 2,
          }}
        />
        
        {/* Rating - Stays at bottom */}
        <Box
          className="rating"
          sx={{
            position: "absolute",
            bottom: "10px",
            left: "10px",
            display: "flex",
            alignItems: "center",
            zIndex: 2,
            transition: "all 0.3s ease",
          }}
        >
          <StarIcon
            sx={{
              fontSize: "14px",
              color: "#ffc107",
              mr: 0.5,
            }}
          />
          <Typography
            sx={{
              color: "white",
              fontSize: "12px",
              fontWeight: "500",
              textShadow: "1px 1px 3px rgba(0, 0, 0, 0.7)",
            }}
          >
            ★ {rating}
          </Typography>
        </Box>
      </Box>
    </Card>
  );
};

/* ======================
   MAIN SECTION
====================== */
const FeaturedTours = () => {
  return (
    <Container maxWidth="lg" 
      sx={{ 
        py: 4, 
        display: "flex", 
        flexDirection: "column", 
        alignItems: "center"
      }}
    >
      <Box
        sx={{
          mb: 3,
          width: "83%",
          textAlign: "left",
        }}
      >
        <Typography variant="h5" fontWeight="700" sx={{ mb: 1 }}>
          Featured Tours
        </Typography>
        <Typography variant="body5" color="text.secondary">
          These alluring destinations are picked just for you.
        </Typography>
      </Box>

      {/* Cards Grid */}
 <Grid container spacing={1.5} justifyContent="center">
  {hotels.map((hotel, index) => (
    <Grid
      item
      xs={12}
      sm={6}
      md={3}
      key={index}
      sx={{
        pl: index === 4 ? "0px !important" : undefined, // ✅ REAL FIX
      }}
    >
      {hotel.isSimpleImage ? (
        <SimpleImageCard {...hotel} />
      ) : hotel.isSpecialCard ? (
        <SpecialCard {...hotel} />
      ) : (
        <HotelCard {...hotel} />
      )}
    </Grid>
  ))}
</Grid>



    </Container>
  );
};

export default FeaturedTours;