"use client";

import { useState, useEffect } from "react";
import { MapPin, Search, ChevronDown } from "lucide-react";
import {
  Grid,
  TextField,
  FormControl,
  OutlinedInput,
  InputLabel,
  InputAdornment,
  Menu,
  MenuItem,
  Button,
  Box,
  Typography,
} from "@mui/material";

import TravellersDropdown from "./TravellersDropDown";

// ------------------------------
// COMPONENT: AirportDropdown (MUI)
// ------------------------------
const AirportDropdown = ({ placeholder = "Flying From", value, onChange }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedAirport, setSelectedAirport] = useState(null);

  const countriesAirports = [
    {
      country: "UAE",
      cities: [
        { city: "Dubai", airport: "Dubai International Airport", code: "DXB" },
        { city: "Abu Dhabi", airport: "Abu Dhabi Intl Airport", code: "AUH" },
      ],
    },
    {
      country: "Pakistan",
      cities: [
        { city: "Karachi", airport: "Jinnah International Airport", code: "KHI" },
        { city: "Lahore", airport: "Allama Iqbal Airport", code: "LHE" },
      ],
    },
    // ... other countries
  ];

  const allAirports = countriesAirports.flatMap((country) =>
    country.cities.map((city) => ({
      city: city.city,
      airport: city.airport,
      code: city.code,
    }))
  );

  const filteredAirports = allAirports.filter(
    (item) =>
      item.city.toLowerCase().includes(searchQuery.toLowerCase()) ||
      item.airport.toLowerCase().includes(searchQuery.toLowerCase()) ||
      item.code.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleSelect = (airport) => {
    setSelectedAirport(airport);
    onChange(`${airport.city} (${airport.code})`);
    setIsOpen(false);
  };

  return (
   <Grid marginLeft={2} paddingTop={1} >
  <FormControl
  fullWidth
  size="small"
  variant="outlined"
  sx={{
    width: 400, // set width
    "& .MuiInputBase-root": { height: 30, fontSize: "12px" }, // input height & font
    "& .MuiInputLabel-root": { fontSize: "12px", top: "-1px" }, // label font & position
  }}
>
  <InputLabel>{placeholder}</InputLabel>
  <OutlinedInput
    value={value || (selectedAirport ? `${selectedAirport.city} (${selectedAirport.code})` : "")}
    onClick={() => setIsOpen(true)}
    onChange={(e) => {
      onChange(e.target.value);
      setSelectedAirport(null);
      setIsOpen(true);
    }}
    endAdornment={
      <InputAdornment position="end" sx={{height:15, width:15}}>
        <ChevronDown
          style={{
            transform: isOpen ? "rotate(180deg)" : "rotate(0deg)",
            transition: "transform 0.2s",
          }}
        />
      </InputAdornment>
    }
    label={placeholder}
    sx={{ width: "100%" }} // make input fill FormControl width
  />
</FormControl>


    </Grid>
  );
};

// ------------------------------
// COMPONENT: SearchButton
// ------------------------------
const SearchButton = () => (
  <Grid item xs={12} sm={1}>
             <Button
               variant="contained"
               color="primary"  
               startIcon={<Search  sx={{height:30, pl:"12px", py:"5px"}}/>}
               sx={{width:"60px", alignItems:"center", height:30,margin:"auto" }}  // adjust button size
             >
           
             </Button>
           </Grid>
);

// ------------------------------
// MAIN COMPONENT: CarSearchForm (MUI Full Width Airports)
// ------------------------------
export default function CarSearchForm() {
  const [flyingFrom, setFlyingFrom] = useState("");
  const [destinationTo, setDestinationTo] = useState("");

  const [pickUpDate, setPickUpDate] = useState("");
  const [pickUpTime, setPickUpTime] = useState("");
  const [dropOffDate, setDropOffDate] = useState("");
  const [dropOffTime, setDropOffTime] = useState("");

  const [travellers, setTravellers] = useState({ adults: 0, children: 0, infants: 0 });

  useEffect(() => {
    const today = new Date();
    const plus7 = new Date();
    plus7.setDate(today.getDate() + 7);

    setPickUpDate(today.toISOString().split("T")[0]);
    setPickUpTime("09:00");
    setDropOffDate(plus7.toISOString().split("T")[0]);
    setDropOffTime("09:00");
  }, []);

  return (
    <Box sx={{ width: "100%", gap: 1, paddingY: 1}}>
      {/* ROW 1: FROM + TO (Full Width) */}
      <Grid container spacing={2} >
        <Grid item xs={12}>
          <AirportDropdown placeholder="Flying From" value={flyingFrom} onChange={setFlyingFrom}
           />
        </Grid>
        <Grid item xs={12}>
          <AirportDropdown placeholder="Destination To" value={destinationTo} onChange={setDestinationTo} />
        </Grid>
      </Grid>

      {/* ROW 2: Pick-Up & Drop-Off + Travellers + Search */}
      <Grid container spacing={2} alignItems="center" sx={{ m: 2 }}>
        <Grid item xs={12} sm={2}>
          <TextField
            fullWidth
            size="small"
            type="date"
            label="Pick-Up Date"
            value={pickUpDate}
            onChange={(e) => setPickUpDate(e.target.value)}
            InputLabelProps={{ shrink: true }}
            inputProps={{ style: { fontSize: 12, height: 15} }}
          />
        </Grid>

        <Grid item xs={12} sm={2}>
          <TextField
            fullWidth
            size="small"
            type="time"
            label="Pick-Up Time"
            value={pickUpTime}
            onChange={(e) => setPickUpTime(e.target.value)}
            InputLabelProps={{ shrink: true }}
            inputProps={{ style: { fontSize: 12, height: 15} }}
          />
        </Grid>

        <Grid item xs={12} sm={2}>
          <TextField
            fullWidth
            size="small"
            type="date"
            label="Drop-Off Date"
            value={dropOffDate}
            onChange={(e) => setDropOffDate(e.target.value)}
            InputLabelProps={{ shrink: true }}
            inputProps={{ style: { fontSize: 12, height: 15 } }}
          />
        </Grid>

        <Grid item xs={12} sm={2}>
          <TextField
            fullWidth
            size="small"
            type="time"
            label="Drop-Off Time"
            value={dropOffTime}
            onChange={(e) => setDropOffTime(e.target.value)}
            InputLabelProps={{ shrink: true }}
            inputProps={{ style: { fontSize: 12, height: 15 } }}
          />
        </Grid>

        <Grid item xs={12} sm={2} width={200} >
          <TravellersDropdown travellers={travellers} onTravellersChange={setTravellers} />
        </Grid>
        

        <Grid item xs={12} sm={2}>
          <SearchButton />
        </Grid>
      </Grid>
      
    </Box>
    
  );
}
