"use client";

import {
  Box,
  Grid,
  Typography,
  Container,
  Card,
  CardMedia,
  CardContent,
  Button,
} from "@mui/material";
import StarIcon from '@mui/icons-material/Star';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';

/* ======================
   DATA
====================== */
const hotels = [
  {
    image: "/styles/FeaturedHotel4.jpg",
    price: "USD 200.00 / Night",
    title: "Movenpick Grand Al Bustan",
    location: "Dubai United Arab Emirates",
    rating: 5,
  },
  {
    image: "https://images.unsplash.com/photo-1566073771259-6a8506099945?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8Mnx8aG90ZWwlMjBkdWJhaXxlbnwwfHwwfHx8MA%3D%3D&auto=format&fit=crop&w=600&q=60",
    price: "USD 320.00 / Night",
    title: "Burj Al Arab",
    location: "Dubai United Arab Emirates",
    rating: 5,
  },
  {
    image: "https://images.unsplash.com/photo-1512917774080-9991f1c4c750?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8M3x8aG90ZWwlMjBkdWJhaXxlbnwwfHwwfHx8MA%3D%3D&auto=format&fit=crop&w=600&q=60",
    price: "USD 275.00 / Night",
    title: "Atlantis The Palm",
    location: "Dubai United Arab Emirates",
    rating: 5,
  },
  // Card 4 with ONLY screenshot design (no hotel info)
  {
    image: "https://images.unsplash.com/photo-1584132967334-10e028bd69f7?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8NXx8aG90ZWwlMjBkdWJhaXxlbnwwfHwwfHx8MA%3D%3D&auto=format&fit=crop&w=600&q=60",
    isSpecialCard: true, // Flag for special design
    title: "Discover great deals on",
    subtitle: "hotels around the world",
    buttonText: "View More",
  },
];

/* ======================
   SPECIAL CARD COMPONENT (with Bottom Gradient)
====================== */
const SpecialCard = ({ image, title, subtitle, buttonText }) => {
  return (
    <Card
      sx={{
        width: "225px", // Same width as other cards
        borderRadius: "12px",
        overflow: "hidden",
        boxShadow: "0px 6px 20px rgba(0,0,0,0.08)",
        transition: "all 0.3s ease",
        cursor: "pointer",
        "&:hover": {
          boxShadow: "0px 8px 24px rgba(0, 0, 0, 0.12)",
          transform: "translateY(-4px)",
        },
        position: "relative",
        height: "100%", // Same height as other cards
      }}
    >
      {/* Full card with gradient overlay */}
      <Box
        sx={{
          height: "100%", // Take full card height
          position: "relative",
          overflow: "hidden",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        {/* Background Image */}
        <CardMedia
          component="img"
          image={image}
          alt={title}
          sx={{
            height: "100%",
            width: "100%",
            objectFit: "cover",
            display: "block",
            backgroundColor: "#f5f5f5",
          }}
        />
        
        {/* Blue Gradient Overlay - Starting from 50% from bottom */}
        <Box
          sx={{
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: 'linear-gradient(to bottom, transparent 50%, #1767e8 100%)',
            opacity: 0.9,
          }}
        />
        
        {/* Text and button overlay */}
        <Box
          sx={{
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            display: "flex",
            flexDirection: "column",
            justifyContent: "flex-end", // Align content to bottom
            alignItems: "center",
            p: 3,
            textAlign: "center",
            pb: 4, // Extra padding at bottom
          }}
        >
          <Typography
            sx={{
              fontWeight: "500",
              color: "white",
              fontSize: "16px",
              mb: 0.5,
              lineHeight: 1.2,
            }}
          >
            {title}
          </Typography>
          
          <Typography
            sx={{
              fontWeight: "700",
              color: "white",
              fontSize: "20px",
              mb: 3,
              lineHeight: 1.2,
            }}
          >
            {subtitle}
          </Typography>
          
          <Button
            variant="contained"
            endIcon={<ArrowForwardIcon />}
            sx={{
              backgroundColor: "white",
              color: "#1767e8",
              fontWeight: "600",
              borderRadius: "8px",
              px: 3,
              py: 1,
              textTransform: "none",
              fontSize: "14px",
              "&:hover": {
                backgroundColor: "#f5f5f5",
                transform: "scale(1.05)",
              },
              boxShadow: "0px 4px 12px rgba(0, 0, 0, 0.3)",
              transition: "all 0.3s ease",
            }}
          >
            {buttonText}
          </Button>
        </Box>
      </Box>
    </Card>
  );
};

/* ======================
   REGULAR CARD COMPONENT
====================== */
const HotelCard = ({ image, price, title, location, rating }) => {
  return (
    <Card
      sx={{
        width: "225px",
        borderRadius: "12px",
        overflow: "hidden",
        boxShadow: "0px 6px 20px rgba(0,0,0,0.08)",
        transition: "all 0.3s ease",
        cursor: "pointer",
        "&:hover": {
          boxShadow: "0px 8px 24px rgba(0, 0, 0, 0.12)",
        },
      }}
    >
      {/* Image Container */}
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          overflow: "hidden",
          height: "150px",
        }}
      >
        <CardMedia
          component="img"
          image={image}
          alt={title}
          sx={{
            maxHeight: "100%",
            maxWidth: "100%",
            objectFit: "cover",
            display: "block",
            backgroundColor: "#f5f5f5",
          }}
        />
      </Box>

      {/* Content */}
      <CardContent sx={{ py: 2 }}>
        {/* Price */}
        <Typography 
          fontWeight="700" 
          color="text.primary" 
          sx={{ 
            mb: 1, 
            fontSize: "12px",
            color: "#333"
          }}
        >
          {price}
        </Typography>

        {/* Hotel Title */}
        <Typography 
          fontWeight="600" 
          sx={{ 
            mb: 0.5, 
            fontSize: "14px",
            lineHeight: 1.3,
            color: "#222"
          }}
        >
          {title}
        </Typography>

        {/* Location */}
        <Typography 
          variant="body2" 
          sx={{ 
            mb: 1,
            color: "#666",
            fontSize: "12px"
          }}
        >
          {location}
        </Typography>

        {/* Star Rating */}
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Box sx={{ display: 'flex' }}>
            {[...Array(5)].map((_, index) => (
              <StarIcon
                key={index}
                sx={{
                  fontSize: '16px',
                  color: index < rating ? '#ffc107' : '#e4e5e9',
                }}
              />
            ))}
          </Box>
          <Typography 
            variant="body2" 
            sx={{ 
              ml: 1, 
              color: '#555',
              fontWeight: 500,
              fontSize: '13px'
            }}
          >
            ★ {rating}
          </Typography>
        </Box>
      </CardContent>
    </Card>
  );
};

/* ======================
   MAIN SECTION
====================== */
const FeaturedHotels = () => {
  return (
    <Container maxWidth="lg" 
      sx={{ 
        py: 4, 
        display: "flex", 
        flexDirection: "column", 
        alignItems: "center"
      }}
    >
      <Box
        sx={{
          mb: 3,
          width: "83%",
          textAlign: "left",
        }}
      >
        <Typography variant="h5" fontWeight="700" sx={{ mb: 1 }}>
          Featured Hotels
        </Typography>
        <Typography variant="body5" color="text.secondary">
          These alluring destinations are picked just for you.
        </Typography>
      </Box>

      {/* Cards Grid */}
      <Grid container spacing={3} justifyContent="center">
        {hotels.map((hotel, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            {hotel.isSpecialCard ? (
              <SpecialCard {...hotel} />
            ) : (
              <HotelCard {...hotel} />
            )}
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default FeaturedHotels;